body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  width: 100%;
  height: 40px;
  background-color: #00f;
  justify-content: center;
  margin-bottom: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
}

input {
  width: 300px;
  height: 20px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  width: 60px;
  height: 22px;
  background-color: #4caf50;
  border: none;
  font-size: 10px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
  display: flex;
  overflow: auto;
}

.photo-card {
  width: 300px;
  box-sizing: border-box;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  margin-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.photo-card img {
  width: 300px;
  height: 250px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.info {
  background-color: #fff;
  justify-content: space-around;
  padding: 5px;
  display: flex;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.info-item {
  margin: 0;
  font-size: 12px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  width: 100px;
  height: 40px;
  text-align: center;
  background-color: #2196f3;
  border: none;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  display: none;
}

